<template>
	<view class="pages">
		<view class="hadersboxs">
			<view class="haderscontents">
				<view class="" @click="back">
					<image class="imgsss"
						src="data:image/png;base64,"
						mode=""></image>
				</view>
				<view class="">{{data.pointname}} -趋势图</view>
				<view class=""> </view>
			</view>
		</view>


		<view class="leave_cont">
			<view class="ul">
				<view class="li" @click="openDatePicker('stime')">
					<text>开始时间:</text>
					<view class="flex1">

						<view class="date">{{stime}}</view>
					</view>
				</view>
				<view class="li" @click="openDatePicker('etime')">
					<text>结束时间:</text>
					<view class="flex1">
						<view class="date">{{etime}}</view>
					</view>
				</view>
			</view>

			<view class="headerrights">
				<view class="rights-lefts" @click="query">
					<image class="leftsimgs"
						src="data:image/png;base64,"
						mode=""></image>
					<text>查询</text>
				</view>
				<view class="rights-rights" @click="goFrequency">
					<image class="rightsimgs"
						src="data:image/png;base64,"
						mode=""></image>
					<text>时频谱</text>
				</view>
			</view>
		</view>


		<view class="content">
			<view class="charts-box">
				<qiun-data-charts class="chartsed" type="line" :opts="opts" :chartData="chartData" :ontouch="true" />
			</view>
		</view>
		<!-- 单选框 -->
		<view class="groupbox">
			<u-radio-group v-model="value" @change="radioGroupChange" size="20px">
				<u-radio label-size="15px" icon-size="5px" @change="radioChange(item.code,item.signal)"
					v-for="(item, index) in radioList" :key="index" :name="item.name" :title="item.title"
					:disabled="item.disabled" active-color="#1890FF" class="uradios">
					{{item.name}}
				</u-radio>
			</u-radio-group>
		</view>
		<u-picker mode="time" v-model="show" :params="params" @confirm="confirm"></u-picker>
	</view>
</template>
<script>
	import {
		gatHeritems,
		gatcurveDatalist
	} from "@/api/api.js"
	var uChartsInstance = {};
	export default {
		data() {

			return {
				unitsed: '',
				signals: '',
				datayAxis: [],
				dataxAxis: [],
				params: {
					year: true,
					month: true,
					day: true,
					hour: true,
					minute: true,
					second: false
				},
				show: false, //时间选择器显示隐藏


				radioList: [{
						name: '加速度',
						disabled: false
					},
					{
						name: '速度',
						disabled: false
					},
					{
						name: '加速度',
						disabled: false
					},
					{
						name: '温度',
						disabled: false
					}
				],
				value: '',
				codeed: '',
				stime: "",
				etime: "",
				currentTime: '',
				chartData: {},
				opts: {
					color: ["#1890FF"],
					dataPointShape: false,
					dataLabel: false,
					padding: [15, 55, 0, 43],
					legend: {
						show: false
					},
					enableScroll: true,
					xAxis: {
						scrollShow: true,
						itemCount:500,
						fontSize: 12,
						type: 'grid',
						gridType: 'dash',
						"labelCount": 5,
						disableGrid: true,
						boundaryGap: "justify",
						scrollAlign: 'bottom', //滚动条初始位置
						scrollBackgroundColor: '#262b41', //默认为 #EFEBEF
						scrollColor: '#262b41', //默认为 #A6A6A6
					},
					yAxis: {
						background:'#262b41',
						fontSize: 12,
						gridType: "dash",
						dashLength: 1,
						tofix: 2
					},
					extra: {
						line: {
							type: "straight",
							width: 1
						}
					}
				},
			};
		},
		async onLoad(options) {

			// #ifdef APP-PLUS
			setTimeout(() => {
				plus.screen.lockOrientation("landscape-primary");
			}, 200);
			// #endif
			this.stime = this.change_date(3);
			this.etime = this.change_date(0);
			let data = JSON.parse(options.data);
			this.data = data
			this.pointname = this.data.pointname
			await this.getitemsList(this.data.pointcode);
			console.log(this.value, '单选')
			let currentRadio = this.radioList.find(item => item.name === this.value)
			let code = currentRadio.code
			this.signals = currentRadio.signal
			this.gatDatalist(code);




		},
		onShow() {},

		onUnload() {

			// #ifdef APP-PLUS
			plus.screen.lockOrientation('portrait-primary');
			// #endif

		},
		onReady() {
			this.getServerData();
		},
		methods: {



			numFilter(value) {
				// 截取当前数据到小数点后两位
				let realVal = parseFloat(value).toFixed(2)
				return realVal
			},







			change_date(days) {
				// 参数表示在当前日期下要增加的天数
				var now = new Date();
				// + 1 代表日期加，- 1代表日期减
				now.setDate((now.getDate() + 1) - 1 * days);
				var year = now.getFullYear();
				var month = now.getMonth() + 1;
				var day = now.getDate();

				var hour = now.getHours();
				var minute = now.getMinutes();
				var second = now.getSeconds();
				if (hour < 10)
					hour = '0' + hour;
				if (minute < 10)
					minute = '0' + minute;
				if (second < 10)
					second = '0' + second;
				if (month < 10) {
					month = '0' + month;
				}
				if (day < 10) {
					day = '0' + day;
				}
				return year + '-' + month + '-' + day + " " + hour + ":" + minute + ":" + second;
			},



			//点击查询
			query() {
				this.gatDatalist(this.pointId)
			},

			// 打开时间选择器
			openDatePicker(time) {
				this.show = !this.show
				this.currentTime = time
			},
			confirm(time) {
				// console.log(time)
				let {
					year: y,
					month: m,
					day: d,
					hour: h,
					minute: mm
				} = time
				// console.log(`${y}/${m}/${d} ${h}:${mm}`)
				this[this.currentTime] = `${y}/${m}/${d} ${h}:${mm}`
			},
			// 选中某个单选框时，由radio时触发
			radioChange(code, signal) {
				this.signals = signal
				let codes = this.gatDatalist(code)
				console.log(this.signals, 999)
			},
			// 选中任一radio时，由radio-group触发
			radioGroupChange(code) {

			},
			//单选框数据
			async getitemsList(pointcode) {
				let res = await gatHeritems(pointcode)
				console.log(res, "单选动态数据")
				if (res.statusCode == 200) {
					this.radioList = res.data
					let radioOption = {
						name: '温度',
						signal: 100,
						code: this.radioList[0].code,
						unit: '℃'
					}
					this.radioList.push(radioOption)
					this.value = res.data[1].name
					console.log(this.value, "选择")
				}
			},

			//获取曲线数据
			gatDatalist(codes) {
				console.log(this.signals, 'signal')
				let params = {
					Origin: "app",
					RequestData: {
						// pointId:"10070010010301",
						pointId: codes,
						signalType: this.signals,
						startTime: this.stime,
						endTime: this.etime
					},
				}

				// console.log(params.RequestData.signalType, "得到code")
				gatcurveDatalist(JSON.stringify(params)).then(res => {
					console.log(1, res)
					// console.log(res, "10")
					if (res.statusCode == 200) {
						// console.log(res, "10")
						this.unitsed = res.data.engineerUnit
						this.datayAxis = res.data.data.map(item => {
							return (item.rms).toFixed(3)
						})
						this.dataxAxis = res.data.data.map(item => {
							return item.dtime
						})
						this.pointId = res.data.pointId
						this.getServerData();
						console.log(2)
					}
				})
			},

			//ucharts-曲线图
			getServerData() {
				setTimeout(() => {
					let res = {
						categories: this.dataxAxis, //x轴
						series: [{ //y轴
							name: '单位:' + (this.unitsed) + "   " + "值",
							data: this.datayAxis,

						}, ],

					};
					this.chartData = JSON.parse(JSON.stringify(res));
					// console.log(this.datayAxis,5425)
				}, 500);
			},



			//跳时频谱
			goFrequency() {
				let data = {
					pointname: this.pointname,
					pointId: this.pointId,
					signal: this.signals,
					name: this.value
				}
				console.log(data.name, "111")
				// return
				if (data.signal == 100) {
					uni.showModal({
						title: "请选择振动趋势",

					})
					return
				}
				if (data.signal == '' || data.name == '') {
					uni.showModal({
						title: "请勾选数据类型"
					})
				}
				if (data.signal || data.name) {
					uni.navigateTo({
						url: `/pages/frequency/index?data=${JSON.stringify(data)}`
					})
				}

			},

			back() {
				uni.navigateBack({
					delta: 1
				})
			},




		}
	};
</script>

<style lang="scss" scoped>
	.charts-box {
		color: #AFDDFF;
		width: 100%;
		height: 215px;
	}

	.chartsed {
		width: 100%;

	}
.canvas{
	background-color: #AFDDFF;
}
	.groupbox {
		margin-left: 10px;
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;

	}


	@import "index.scss";
</style>
